.button {
    color: white;
    font-family: "Arial";
    font-size: 12px;
    padding: 6px 10px;
    margin: 2px;
    border: 0;
    border-radius: 4px;
    transition: 0.3s;
}

.success {
    background: darkgreen;
}

.success:hover {
    background: rgb(0, 75, 0);
    box-shadow: 0 1px 2px 0 rgba(255,255,255,0.2);
}

.danger {
    background: darkred;
}

.danger:hover {
    background: rgb(100, 0, 0);
    box-shadow: 0 1px 2px 0 rgba(255,255,255,0.2);
}

